.searchBarContainer {
  background-color: plum;
  border-radius: 50px;
  border: 1px solid #f2f1f6;
  box-shadow: 0px 5px 5px #b8b7bc;
  margin: auto;
  margin-left: 1rem;
  margin-bottom: 1rem;
  width: clamp(250px, 50%, 800px);
}

.searchBar {
  display: flex;
  border: 2px solid back;
  border-radius: 50px;
  background-color: #fff;
}

.searchInput {
  flex: 1;
  padding: 10px;
  border: none;
  outline: none;
  font-size: 16px;
  border-radius: 50px;
}

.searchButton {
  background-color: none;
  color: rgb(0, 0, 0);
  border: none;
  padding: 10px;
  padding-left: 15px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 100%;
  background-color: #fff;
}

@media (min-width: 550px) {
  .searchBarContainer {
    margin-left: 2rem;
    width: clamp(170px, 50%, 800px);
    margin: 1rem;
  }
}
.headerContainer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-flow: row wrap;
  background-color: #fff;
  border-bottom: 2px solid #dbdade;
  margin-bottom: 1rem;
}